<template>
  <div class="freePathEdit" ref="freePathEdit">
    <div id="setFreePath"></div>
    <div class="auxiliary_line_box">
      <div v-show="middleData.show" class="net_line_middle" :style="middleStyle"></div>
      <div v-show="centerData.show" class="net_line_center" :style="centerStyle"></div>
    </div>
    <div v-drag class="free-path-control" @mousemove="controlMove" @dblclick="controlDbClick">
      <div class="first-point" data-action="firstPoint" v-if="status === 'paint'" :style="{left: firstP[0] - 4 + 'px', top: firstP[1] - 4 + 'px'}"></div>
      <div class="point-box" v-if="status === 'edit'">
        <div v-for="(p, i) in point">
          <div v-if="type === 'bessel'" class="left-point" :data-action="'pointLeft-' + i" :style="{left: p[0] - 4 + 'px', top: p[1] - 4 + 'px'}"></div>
          <div class="center-point" :class="{active: i === pIndex}" :data-action="'pointCenter-' + i" :style="{left: p[2] - 4 + 'px', top: p[3] - 4 + 'px'}"></div>
          <div v-if="type === 'bessel'" class="right-point" :data-action="'pointRight-' + i" :style="{left: p[4] - 4 + 'px', top: p[5] - 4 + 'px'}"></div>
        </div>
      </div>
    </div>
    <div v-show="status === 'play'" class="path-preview">
      <div class="play-boll" :style="{top: bollP.y + 'px', left: bollP.x + 'px'}"></div>
    </div>
  </div>
</template>
<script>
import { posix, parse } from 'path';
export default {
  name: 'freePathEdit',
  data() {
    return {
      type: 'line', //bessel
      status: 'paint',
      dragHandle: false,
      pos: {
        x: 0,
        y: 0
      },
      firstP: [0, 50],
      point: [],
      pointC: [],
      lineDom: null,
      pathDom: null,
      mpos: null,
      action: '',
      pIndex: -1,
      pathZ: false,
      adsorbArr: [],
      adsorbData: null,
      middleData:{
        show: false,
        width: 0,
        top: 0,
        left: 0
      },
      centerData: {
        show:false,
        height: 0,
        top: 0,
        left: 0
      },
      bollP: {
        x: 0,
        y: 0
      },
      animFrame: null
    }
  },
  beforeRouteEnter (to, from, next) {
    let type = 'line';
    to.params.type && (type = to.params.type);
    next(vm => vm.type = type);
  },
  // 路由改变前，组件就已经渲染完了
  // 逻辑稍稍不同
  beforeRouteUpdate (to, from, next) {
    let type = 'line';
    to.params.type && (type = to.params.type);
    next(vm => {
      if(vm.type !== type) {
        vm.init();
      }
    })
  },
  computed: {
    middleStyle() {
      const style = {};
      style.width = this.middleData.width + 'px';
      style.top = this.middleData.top + 'px';
      style.left = this.middleData.left + 'px';
      return style;
    },
    centerStyle() {
      const style = {};
      style.height = this.centerData.height + 'px';
      style.top = this.centerData.top + 'px';
      style.left = this.centerData.left + 'px';
      return style;
    }
  },
  mounted() {
    this.init()
    this.$bus.on('keydown', data => {
      if(!data.consist && (data.keyCode === 46 || data.keyCode === 8)) {
        if(this.status === 'edit') {
          this.deletePoint()
        }
      } else if(!data.consist && data.keyCode === 32) {
        if(this.status === 'edit') {
          this.play()
        } else if(this.status === 'play') {
          this.play(true)
        }
      }
    })
  },
  methods: {
    init() {
      let {x: x, y: y} = this.$refs.freePathEdit.getBoundingClientRect()
      this.pos.x = x
      this.pos.y = y
      this.pathZ = false;
      this.firstP = [100, 240]
      this.point.push([100, 240, 100, 240, 100, 240])
      this.initSvgDom();
    },
    initSvgDom() {
      // 初始化SVG
      $('#setFreePath').empty();
      let svgDom = SVG('setFreePath').size(960, 540);
      this.pathDom = svgDom.group()
      this.pathDom.path('').attr({
        fill: 'none',
        stroke: 'rgb(0, 0, 0)',
        'stroke-linecap': 'round',
        'stroke-linejoin': 'round',
        'stroke-width': 2
      });
      this.lineDom = svgDom.group()
      svgDom = null
    },
    dragDown(dis, action, handle) {
      this.dragHandle = handle
      if(this.status === 'paint') {
        let px = dis.x - this.pos.x,
            py = dis.y - this.pos.y;
        if(action === 'firstPoint' && this.point.length > 1) {
          this.pathZ = true;
          this.status = 'edit'
          this.freePathSvg(this.point)
          for(let val of this.point) {
            this.linePathSvg(val, true)
          }
        }
        this.pointC = [px, py, px, py, px, py]
      } else if(this.status === 'edit') {
        if(!action) return;
        let actionS = action.split('-');
        this.action = actionS[0]
        this.pIndex = + actionS[1]
        this.mpos = {
          x: dis.x,
          y: dis.y,
          arr: [].concat(this.point[this.pIndex])
        }
        this._getAdsorbArr();
      }
    },
    dragMove(dis, action, handle) {
      this.dragHandle = handle
      if(this.status === 'paint') {
        let px2 = dis.x - this.pos.x,
            py2 = dis.y - this.pos.y;
        let {x: px0, y: py0} = this.symmetryP(px2, py2, this.pointC[2], this.pointC[3])
        this.pointC = [px0, py0, this.pointC[2], this.pointC[3], px2, py2]
        this.linePathSvg(this.pointC)
        this.freePathSvg(this.point.concat([this.pointC]))
      } else if(this.status === 'edit') {
        if(!this.action) return;
        let change = null,
            change1 = null,
            changeA = [],
            arr = this.mpos.arr;
        if(this.action === 'pointLeft') {
          change = {
            x: arr[0] + dis.x - this.mpos.x,
            y: arr[1] + dis.y - this.mpos.y
          }
          change1 = this.symmetryP(change.x, change.y, arr[2], arr[3]);
          changeA = [change.x, change.y, arr[2], arr[3], change1.x, change1.y]
        }else if(this.action === 'pointRight'){
          change = {
            x: arr[4] + dis.x - this.mpos.x,
            y: arr[5] + dis.y - this.mpos.y
          }
          change1 = this.symmetryP(change.x, change.y, arr[2], arr[3]);
          changeA = [change1.x, change1.y, arr[2], arr[3], change.x, change.y]
        }else if(this.action === 'pointCenter') {
          if(this.pIndex === 0) return;
          change = {
            x: this.mpos.x - dis.x,
            y: this.mpos.y - dis.y
          }
          this.adsorbData = this._splitAbsorbArr([arr[2]-change.x, arr[3]-change.y]);
          let pos = this._getAdsorbPos(this.adsorbData)
          
          if(typeof pos.x === 'number') change.x = arr[2] - pos.x;
          if(typeof pos.y === 'number') change.y = arr[3] - pos.y;
          changeA = [arr[0]-change.x, arr[1]-change.y, arr[2]-change.x, arr[3]-change.y, arr[4]-change.x, arr[5]-change.y];
        }
        this.$set(this.point, this.pIndex, changeA);
        this.freePathSvg()
        this.type === 'bessel' &&this.updateLineSvg(this.pIndex, changeA)
      }
    },
    dragUp(dis, action, handle) {
      this.dragHandle = handle
      if(this.status === 'paint') {
        this.point.push(this.pointC)
        this.freePathSvg()
        this.lineDom.clear()
      } else if(this.status === 'edit'){
        this.action = ''
        this.middleData.show = false;
        this.centerData.show = false;
      }
    },
    controlMove(e) {
      // 鼠标移动事件
      if(this.status !== 'paint') return;
      let px2 = e.pageX - this.pos.x,
          py2 = e.pageY - this.pos.y;
      if(this.dragHandle) {
        let {x: px0, y: py0} = this.symmetryP(px2, py2, this.pointC[2], this.pointC[3])
        this.pointC = [px0, py0, this.pointC[2], this.pointC[3], px2, py2]
        this.linePathSvg(this.pointC)
        this.freePathSvg(this.point.concat([this.pointC]))
      } else {
        this.freePathSvg(this.point.concat([[px2, py2, px2, py2, px2, py2]]))
      }
    },
    controlDbClick() {
      if(this.status !== 'paint') return;
      this.status = 'edit';
      this.point.pop()
      this.pathZ = false;
      this.freePathSvg()
      this.lineDom.clear()
      for(let i = 0; i< this.point.length; i++) {
        this.linePathSvg(this.point[i], true)
      }
    },
    freePathSvg(data) {
      // 绘画path
      let path = '';
      let point = data || this.point;
      path = this.getPath(point)
      this.pathDom.first().attr({
        d: path
      })
    },
    linePathSvg(data, clear) {
      if(this.type === 'line') return;
      let path = '';
      !clear && this.lineDom.clear()
      path = 'M' + data.join(',');
      this.lineDom.path(path).attr({
        fill: 'none',
        stroke: 'blue',
        'stroke-linecap': 'round',
        'stroke-linejoin': 'round',
        'stroke-width': 1
      });
    },
    updateLineSvg(i, data) {
      if(this.type === 'line') return;
      let path = '';
      path = 'M' + data.join(',');
      this.lineDom.get(i).attr({
        d: path
      })
    },
    getPath(point) {
      let path = ''
      if(this.type === 'line') {
        point.forEach((e, i) => {
          if (i === 0) {
            path = `M ${e[2]} ${e[3]}`
          }else {
            path += ` ${e[2]} ${e[3]}`
          }
        });
        if(this.pathZ && point.length > 2) {
          path += ` ${point[0][2]} ${point[0][3]} Z`
        }
      }else {
        point.forEach((e, i) => {
          if (i === 0) {
            path = `M ${e[2]} ${e[3]}`
          }else {
            path += ` C ${point[i-1][4]} ${point[i-1][5]} ${e[0]} ${e[1]} ${e[2]} ${e[3]}`
          }
        });
        if(this.pathZ) {
          path += ` C ${point[point.length-1][4]} ${point[point.length-1][5]} ${point[0][0]} ${point[0][1]} ${point[0][2]} ${point[0][3]} Z`
        }
      }
      return path;
    },
    symmetryP(x1, y1, x0, y0) {
      return {
        x: 2 * x0 - x1,
        y: 2 * y0 - y1 
      }
    },
    _getAdsorbArr: function() {
      var adsorbArr = []
      this.point.forEach((e, i) => {
        if(i !== this.pIndex) {
          adsorbArr.push(e[2], e[3])
        }
      })
      this.absorbArr = adsorbArr
    },
    _getAdsorbPos(arr) {
        // 移动吸附到当前出现的辅助线
        var pos = {};
        var _this = this;
        arr.forEach(function(val, i) {
            if(val.v) {
                if((i+2)%2 === 0) {
                  pos.x = val.v;
                  pos.p1 = val.p1
                  pos.p2 = val.p2
                } else {
                  pos.y = val.v;
                  pos.p3 = val.p1
                  pos.p4 = val.p2
                }
            }
        })
        this._showNetLine(pos)
        return pos
    },
    _splitAbsorbArr(arr) {
      // 获取这个场景页中需要判断吸附的数据，并分为两组，分别为垂直线和水平线
      let Arr1 = this.absorbArr,
          Arr2 = [],
          Arr3 = [];
        Arr1.forEach(function(k, i) {
          if((i+2)%2 === 0) {
            Arr2.push(k);
          } else {
            Arr3.push(k);
          }
        })
      return this._compareArr(arr, Arr2, Arr3)
    },
    _compareArr(arr1, arr2, arr3) {
        // 获取这个元素在变化的时候需要吸附的信息
        // data数据中分别存着外接矩形需要吸附的数据，顺序和外接矩形数据相同
        let data = [{},{}],
            _arguments = arguments;
        arr1.forEach(function(k, i) {
            var index = 2;
            if((i+2)%2 === 0) index = 1;
            _arguments[index].forEach(function(v, j) {
                if (Math.floor(Math.abs(k-v)/5) ===0) {
                    data[i].v = v;
                    data[i].p1 =  _arguments[-index+3][j]
                    data[i].p2 = arr1[-i+1]
                }
            })
        })
        return data;
    },
    _showNetLine(pos) {
      this.middleData.show = false;
      this.centerData.show = false;
      if(typeof pos.y === 'number') {
        this.middleData.show = true
        this.middleData.left = Math.min(pos.p3, pos.p4) - 20
        this.middleData.width = 40 + Math.abs(pos.p3 - pos.p4)
        this.middleData.top = pos.y
      }
      if(typeof pos.x === 'number') {
        this.centerData.show = true
        this.centerData.top = Math.min(pos.p1, pos.p2) - 20
        this.centerData.height = 40 + Math.abs(pos.p1 - pos.p2)
        this.centerData.left = pos.x
      }
    },
    deletePoint() {
      if(this.pIndex >0 && this.point.length > 2) {
        this.point.splice(this.pIndex, 1)
        let lineDom =  this.lineDom.get(this.pIndex)
        lineDom && lineDom.remove();
        this.freePathSvg();
        this.pIndex--;
      }
    },
    play(stop) {
      if(stop) {
        window.cancelAnimationFrame(this.animFrame);
        this.status = 'edit';
        this.lineDom.show();
        return;
      }
      let dom = this.pathDom.first();
      let length = dom.length();
      let start = 0,
          sn = Math.round(length / 60 / 4),
          pos = null,
          step = null,
          animFrame = null;
      this.lineDom.hide();
      this.status = 'play';

      step =  (timestamp) => {
        start += sn
        if(start <= length) {
          pos = dom.pointAt(start)
          this.bollP.x = pos.x - 10
          this.bollP.y = pos.y - 10;
          this.animFrame = window.requestAnimationFrame(step);
        } else {
          window.cancelAnimationFrame(this.animFrame);
          this.status = 'edit';
          this.lineDom.show();
        }
      }

      this.animFrame = window.requestAnimationFrame(step);
    }
  }
}
</script>
<style lang="scss" scoped>
.freePathEdit {
  width: 960px;
  height: 540px;
  margin: 50px auto;
  position: relative;
  background-color:  rgba(0,0,0,.05);
}
#setFreePath, .free-path-control, .path-preview {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.free-path-control {
  z-index: 10;

  div.first-point {
    position: absolute;
    width: 9px;
    height: 9px;
    border: 1px solid blue;
    box-sizing: border-box;
    cursor: pointer;
  }
}

.point-box {
  position: absolute;
  top: 0;
  left: 0;

  > div {
    position: absolute;
    top: 0;
    left: 0;

    > div {
      position: absolute;
      width: 9px;
      height: 9px;
      box-sizing: border-box;
    }
  }

  .left-point, .right-point {
    border-radius: 50%;
    background-color: blue;
    cursor: move;
  }
  .center-point {
    border: 1px solid blue;
    cursor: move;
  }
  .center-point.active {
    background-color: blue;
  }
}
.auxiliary_line_box {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  div {
    position: absolute;
    opacity: .6;
  }

  div:nth-child(even) {
    height: 100%;
    width: 1px;
    border-left: 1px solid #fff;

    &::before {
      position: absolute;
      display: block;
      content: '';
      border-left: 1px dashed #0000ff;
      height: 100%;
      width: 1px;
      left: -1px;
      box-sizing: content-box;
      pointer-events: none;
    }
  }
  div:nth-child(odd) {
    width: 100%;
    height: 1px;
    border-top: 1px solid #fff;

    &::before {
      position: absolute;
      display: block;
      content: '';
      border-top: 1px dashed #0000ff;
      height: 1px;
      width: 100%;
      top: -1px;
      box-sizing: content-box;
      pointer-events: none;
    }
  }
}
.play-boll {
  position: absolute;
  width: 21px;
  height: 21px;
  border-radius: 100%;
  background-color: aqua;
}
</style>
